/*_Font_*/
@font-face {
  font-family: 'Lato';
  src: urlCss('fonts/Lato-Bold/lato-bold.eot');
  src: urlCss('fonts/Lato-Bold/lato-bold.eot') format('embedded-opentype'),
  urlCss('fonts/Lato-Bold/lato-bold.woff2') format('woff2'),
  urlCss('fonts/Lato-Bold/lato-bold.woff') format('woff'),
  urlCss('fonts/Lato-Bold/lato-bold.ttf') format('truetype'),
  urlCss('fonts/Lato-Bold/lato-bold.svg') format('svg');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Lato';
  src: urlCss('fonts/Lato-Light/lato-light.eot');
