  urlCss('fonts/Lato-Light/lato-light.ttf') format('truetype'),
  urlCss('fonts/Lato-Light/lato-light.svg') format('svg');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Lato';
  src: urlCss('fonts/Lato-Regular/lato-regular.eot');
  src: urlCss('fonts/Lato-Regular/lato-regular.eot') format('embedded-opentype'),
  urlCss('fonts/Lato-Regular/lato-regular.woff2') format('woff2'),
  urlCss('fonts/Lato-Regular/lato-regular.woff') format('woff'),
  urlCss('fonts/Lato-Regular/lato-regular.ttf') format('truetype'),
  urlCss('fonts/Lato-Regular/lato-regular.svg') format('svg');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Philosopher';
