}

@font-face {
  font-family: 'fontello';
  src: urlCss('fonts/fontello/fontello.eot?77419675');
  src: urlCss('fonts/fontello/fontello.eot?77419675#iefix') format('embedded-opentype'),
  urlCss('fonts/fontello/fontello.woff2?77419675') format('woff2'),
  urlCss('fonts/fontello/fontello.woff?77419675') format('woff'),
  urlCss('fonts/fontello/fontello.ttf?77419675') format('truetype'),
  urlCss('fonts/fontello/fontello.svg?77419675#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
